import warnings from './warnings'

const body = document.getElementsByTagName('body')
const label = document.querySelector('.label')
const form = document.querySelector('.content__form')
const inputs = document.querySelectorAll('.content__form--input')

// —Å–æ—Å—Ç–æ—è–Ω–∏–µ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –≤ –ø–æ–ø–∞–ø—á–∏–∫–∞—Ö

let labelText = warnings.warn

// –ø–æ–∫–∞–∑—ã–≤–∞–µ–º body –ø–æ—Å–ª–µ –ø–æ–ª–Ω–æ–π –∑–∞–≥—Ä—É–∑–∫–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã

window.addEventListener('load', (e) => (body[0].style.opacity = 1))

// —Å–∞–±–º–∏—Ç —Ñ–æ—Ä–º—ã

form.addEventListener('submit', (evt) => {
    evt.preventDefault()

    showLabel(labelText)
})

// –¥–µ–ª–∞–µ–º –∏–∑ –ø—Å–µ–≤–¥–æ–º–∞—Å—Å–∏–≤–∞ –º–∞—Å—Å–∏–≤ –∏ –ø–µ—Ä–µ–±–∏—Ä–∞–µ–º, –Ω–∞—Ö–æ–¥–∏–º –Ω–∞—à–∏ –µ–ª–µ–º–µ–Ω—Ç—ã –∏–∑ DOM –∏ –≤—ã–∑—ã–≤–∞–µ–º —Ñ—É–Ω-—Ü–∏—é –≤–∞–ª–∏–¥–∞—Ü–∏–∏

Array.from(inputs).forEach((items) => {
    const input = items.children[0]
    const span = items.children[1]

    input.addEventListener('change', (e) => {
        validation(input, items, span)
    })
})

// –ø—Ä–æ–≤–µ—Ä—è–µ–º –¥–ª–∏–Ω–Ω—É –Ω–∞–±—Ä–∞–Ω–æ–≥–æ —Ç–µ–∫—Å—Ç–∞ –∏ –Ω–∞ –ª–∞—Ç—ã–Ω–∏—Ü—É

function validation(input, item, span) {
    let inputValue = input.value.length
    if (inputValue === 0) {
        item.classList.remove('error')
        item.value = ''
        labelText = warnings.warn
        // btn.disabled = false
    } else if (inputValue < 2 || inputValue > 10) {
        item.classList.add('error')
        span.innerHTML = `–í –ø–æ–ª–µ ${input.name} –æ—Ç 2 –¥–æ 10 —Å–∏–º–≤–æ–ª–æ–≤`
        labelText = warnings.error
        // btn.disabled = true
    } else {
        item.classList.remove('error')
        labelText = warnings.success
        // btn.disabled = false
    }

    if (input.value.match(/[–ê-—è–Å—ë]/)) {
        item.classList.add('error')
        span.innerHTML = `–¢–æ–ª—å–∫–æ –ª–∞—Ç–∏–Ω–∏—Ü–∞`
        labelText = warnings.error
    }
}

// —Ñ—É–Ω-—Ü–∏—è –æ—Ç–≤–µ—á–∞—é—â–∞—è –∑–∞ –ø–æ–ø–∞–ø—ã —Å –ø—Ä–µ–¥—É–ø—Ä–µ–∂–¥–µ–Ω–∏—è–º–∏

function showLabel(value) {
    label.innerHTML = value
    label.classList.add('active')
    removeClass(label, 'active')

    if (value === warnings.error) {
        label.classList.add('error')
        removeClass(label, 'error')
    } else if (value === warnings.warn) {
        label.classList.add('warn')
        removeClass(label, 'warn')
    } else {
        label.classList.add('success')
        removeClass(label, 'success')
    }
}

// —É–¥–∞–ª—è–µ–º –∫–ª–∞—Å—ã —á–µ—Ä–µ–∑ –∫–∞–∫–æ–µ-—Ç–æ –≤—Ä–µ–º—è

function removeClass(item, className) {
    setTimeout(() => {
        item.classList.remove(className)
    }, 2000)
}
